import React, { useState, useEffect } from 'react';
import Table from '../common/Table';
import Form from '../common/Form';

interface Municipio {
  id: number;
  nombre_municipio: string;
  codigo_municipio: string;
  id_departamento: number;
}

interface Departamento {
  id: number;
  nombre_departamento: string;
}

const MunicipiosPage: React.FC = () => {
  const [municipios, setMunicipios] = useState<Municipio[]>([]);
  const [departamentos, setDepartamentos] = useState<Departamento[]>([]);
  const [editingMunicipio, setEditingMunicipio] = useState<Municipio | null>(null);
  const [formValues, setFormValues] = useState({
    nombre_municipio: '',
    codigo_municipio: '',
    id_departamento: ''
  });

  const fetchMunicipios = async () => {
    try {
      const response = await fetch('http://localhost:3001/api/municipios');
      const data = await response.json();
      setMunicipios(data);
    } catch (error) {
      console.error('Error al cargar municipios:', error);
      alert('Error al cargar los municipios');
    }
  };

  const fetchDepartamentos = async () => {
    try {
      const response = await fetch('http://localhost:3001/api/departamentos');
      const data = await response.json();
      setDepartamentos(data);
    } catch (error) {
      console.error('Error al cargar departamentos:', error);
      alert('Error al cargar los departamentos');
    }
  };

  useEffect(() => {
    fetchMunicipios();
    fetchDepartamentos();
  }, []);

  const handleFormChange = (name: string, value: string) => {
    setFormValues(prev => ({ ...prev, [name]: value }));
  };

  const handleSubmit = async () => {
    try {
      const url = editingMunicipio
        ? `http://localhost:3001/api/municipios/${editingMunicipio.id}`
        : 'http://localhost:3001/api/municipios';
      
      const method = editingMunicipio ? 'PUT' : 'POST';
      
      const response = await fetch(url, {
        method,
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          ...formValues,
          id_departamento: parseInt(formValues.id_departamento)
        }),
      });

      if (!response.ok) {
        throw new Error('Error en la operaci√≥n');
      }

      await fetchMunicipios();
      setFormValues({ nombre_municipio: '', codigo_municipio: '', id_departamento: '' });
      setEditingMunicipio(null);
    } catch (error) {
      console.error('Error al guardar municipio:', error);
      alert('Error al guardar el municipio');
    }
  };

  const handleEdit = (municipio: Municipio) => {
    setEditingMunicipio(municipio);
    setFormValues({
      nombre_municipio: municipio.nombre_municipio,
      codigo_municipio: municipio.codigo_municipio,
      id_departamento: municipio.id_departamento.toString(),
    });
  };

  const handleDelete = async (municipio: Municipio) => {
    if (!window.confirm('¬øEst√° seguro de eliminar este municipio?')) {
      return;
    }

    try {
      const response = await fetch(`http://localhost:3001/api/municipios/${municipio.id}`, {
        method: 'DELETE',
      });

      if (!response.ok) {
        throw new Error('Error al eliminar');
      }

      await fetchMunicipios();
    } catch (error) {
      console.error('Error al eliminar municipio:', error);
      alert('Error al eliminar el municipio');
    }
  };

  const formFields = [
    {
      name: 'nombre_municipio',
      label: 'Nombre del Municipio',
      type: 'text' as const,
    },
    {
      name: 'codigo_municipio',
      label: 'C√≥digo del Municipio',
      type: 'text' as const,
    },
    {
      name: 'id_departamento',
      label: 'Departamento',
      type: 'select' as const,
      options: departamentos.map(d => ({
        value: d.id,
        label: d.nombre_departamento
      }))
    }
  ];

  const tableHeaders = ['ID', 'Nombre', 'C√≥digo', 'Departamento'];

  return (
    <div className="container mx-auto p-4">
      <h1 className="text-2xl font-bold mb-6">Gesti√≥n de Municipios</h1>
      
      <div className="bg-white rounded-lg shadow p-6 mb-6">
        <h2 className="text-xl font-semibold mb-4">
          {editingMunicipio ? 'Editar Municipio' : 'Nuevo Municipio'}
        </h2>
        <Form
          fields={formFields}
          values={formValues}
          onChange={handleFormChange}
          onSubmit={handleSubmit}
          submitLabel={editingMunicipio ? 'Actualizar' : 'Crear'}
        />
      </div>

      <div className="bg-white rounded-lg shadow">
        <div className="p-6">
          <h2 className="text-xl font-semibold mb-4">Lista de Municipios</h2>
          <Table
            headers={tableHeaders}
            data={municipios.map(m => ({
              id: m.id,
              nombre: m.nombre_municipio,
              codigo: m.codigo_municipio,
              departamento: departamentos.find(d => d.id === m.id_departamento)?.nombre_departamento || 'No encontrado'
            }))}
            onEdit={handleEdit}
            onDelete={handleDelete}
          />
        </div>
      </div>
    </div>
  );
};

export default MunicipiosPage;
