import React, { useState, useEffect } from 'react';
import Table from '../common/Table';
import Form from '../common/Form';

interface Colegio {
  id: number;
  nombre_colegio: string;
  codigo_colegio: string;
  id_municipio: number;
}

interface Municipio {
  id_municipio: number;
  nombre_municipio: string;
  codigo_municipio: string;
  id_departamento: number;
}

const ColegiosPage: React.FC = () => {
  const [colegios, setColegios] = useState<Colegio[]>([]);
  const [municipios, setMunicipios] = useState<Municipio[]>([]);
  const [editingColegio, setEditingColegio] = useState<Colegio | null>(null);
  const [formValues, setFormValues] = useState({
    nombre_colegio: '',
    codigo_colegio: '',
    id_municipio: 'default'
  });
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  const fetchColegios = async () => {
    try {
      setLoading(true);
      setError(null);
      const response = await fetch('http://localhost:3001/api/colegios');
      if (!response.ok) {
        throw new Error('Error al cargar los colegios');
      }
      const data = await response.json();
      console.log('Colegios cargados:', JSON.stringify(data, null, 2));
      setColegios(data);
    } catch (error) {
      console.error('Error al cargar colegios:', error);
      setError('Error al cargar los colegios. Por favor, intente nuevamente.');
    } finally {
      setLoading(false);
    }
  };

  const fetchMunicipios = async () => {
    try {
      setError(null);
      const response = await fetch('http://localhost:3001/api/municipios');
      if (!response.ok) {
        throw new Error('Error al cargar los municipios');
      }
      const data = await response.json();
      console.log('Municipios cargados:', JSON.stringify(data, null, 2));
      setMunicipios(data);
    } catch (error) {
      console.error('Error al cargar municipios:', error);
      setError('Error al cargar los municipios. Por favor, intente nuevamente.');
    }
  };

  useEffect(() => {
    const loadData = async () => {
      try {
        setLoading(true);
        await fetchMunicipios();
        await fetchColegios();
      } catch (error) {
        console.error('Error al cargar datos:', error);
        setError('Error al cargar los datos. Por favor, intente nuevamente.');
      } finally {
        setLoading(false);
      }
    };

    loadData();
  }, []);

  const handleFormChange = (name: string, value: string) => {
    setFormValues(prev => ({ ...prev, [name]: value }));
  };

  const handleSubmit = async () => {
    if (!formValues.id_municipio || formValues.id_municipio === 'default') {
      setError('Por favor seleccione un municipio');
      return;
    }

    try {
      setError(null);
      const url = editingColegio
        ? `http://localhost:3001/api/colegios/${editingColegio.id}`
        : 'http://localhost:3001/api/colegios';
      
      const method = editingColegio ? 'PUT' : 'POST';
      
      const response = await fetch(url, {
        method,
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          nombre_colegio: formValues.nombre_colegio,
          codigo_colegio: formValues.codigo_colegio,
          id_municipio: parseInt(formValues.id_municipio)
        }),
      });

      if (!response.ok) {
        throw new Error('Error en la operaci√≥n');
      }

      await fetchColegios();
      setFormValues({ nombre_colegio: '', codigo_colegio: '', id_municipio: 'default' });
      setEditingColegio(null);
    } catch (error) {
      console.error('Error al guardar colegio:', error);
      setError('Error al guardar el colegio. Por favor, intente nuevamente.');
    }
  };

  const handleEdit = (colegio: Colegio) => {
    const municipio = municipios.find(m => m.id_municipio === colegio.id_municipio);
    console.log('Editando colegio:', colegio);
    console.log('Municipio encontrado:', municipio);

    setEditingColegio(colegio);
    setFormValues({
      nombre_colegio: colegio.nombre_colegio || '',
      codigo_colegio: colegio.codigo_colegio || '',
      id_municipio: colegio.id_municipio ? colegio.id_municipio.toString() : 'default'
    });
  };

  const handleDelete = async (colegio: Colegio) => {
    if (!window.confirm('¬øEst√° seguro de eliminar este colegio?')) {
      return;
    }

    try {
      setError(null);
      const response = await fetch(`http://localhost:3001/api/colegios/${colegio.id}`, {
        method: 'DELETE',
      });

      if (!response.ok) {
        throw new Error('Error al eliminar');
      }

      await fetchColegios();
    } catch (error) {
      console.error('Error al eliminar colegio:', error);
      setError('Error al eliminar el colegio. Por favor, intente nuevamente.');
    }
  };

  const formFields = [
    {
      name: 'nombre_colegio',
      label: 'Nombre del Colegio',
      type: 'text' as const,
    },
    {
      name: 'codigo_colegio',
      label: 'C√≥digo del Colegio',
      type: 'text' as const,
    },
    {
      name: 'id_municipio',
      label: 'Municipio',
      type: 'select' as const,
      options: [
        { value: 'default', label: 'Seleccione un municipio' },
        ...municipios
          .filter(m => m && m.id_municipio && m.nombre_municipio)
          .map(m => ({
            value: m.id_municipio.toString(),
            label: m.nombre_municipio
          }))
      ]
    }
  ];

  const tableHeaders = ['ID', 'Nombre', 'C√≥digo', 'Municipio'];

  const getMunicipioNombre = (id_municipio: number) => {
    const municipio = municipios.find(m => m.id_municipio === id_municipio);
    return municipio ? municipio.nombre_municipio : 'No encontrado';
  };

  return (
    <div className="space-y-8">
      <div className="bg-white rounded-xl shadow-md overflow-hidden">
        <div className="p-6">
          <div className="flex items-center justify-between mb-6">
            <h2 className="text-xl font-semibold text-gray-900">
              {editingColegio ? 'Editar Colegio' : 'Nuevo Colegio'}
            </h2>
            {editingColegio && (
              <button
                onClick={() => {
                  setEditingColegio(null);
                  setFormValues({ nombre_colegio: '', codigo_colegio: '', id_municipio: 'default' });
                }}
                className="text-sm text-gray-600 hover:text-gray-900"
              >
                Cancelar edici√≥n
              </button>
            )}
          </div>
          <Form
            fields={formFields}
            values={formValues}
            onChange={handleFormChange}
            onSubmit={handleSubmit}
            submitLabel={editingColegio ? 'Actualizar' : 'Crear'}
          />
        </div>
      </div>

      <div className="bg-white rounded-xl shadow-md overflow-hidden">
        <div className="p-6">
          <div className="flex items-center justify-between mb-6">
            <h2 className="text-xl font-semibold text-gray-900">Lista de Colegios</h2>
            <div className="flex space-x-4">
              <button
                onClick={fetchMunicipios}
                className="inline-flex items-center rounded-md bg-gray-50 px-3 py-1 text-sm font-medium text-gray-700 hover:bg-gray-100 focus:outline-none focus:ring-2 focus:ring-gray-500 focus:ring-offset-2 transition-colors duration-200"
              >
                Actualizar Municipios
              </button>
              <button
                onClick={fetchColegios}
                className="inline-flex items-center rounded-md bg-gray-50 px-3 py-1 text-sm font-medium text-gray-700 hover:bg-gray-100 focus:outline-none focus:ring-2 focus:ring-gray-500 focus:ring-offset-2 transition-colors duration-200"
              >
                Actualizar Colegios
              </button>
            </div>
          </div>

          {error && (
            <div className="mb-4 rounded-md bg-red-50 p-4">
              <div className="flex">
                <div className="flex-shrink-0">
                  <svg className="h-5 w-5 text-red-400" viewBox="0 0 20 20" fill="currentColor">
                    <path fillRule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zM8.707 7.293a1 1 0 00-1.414 1.414L8.586 10l-1.293 1.293a1 1 0 101.414 1.414L10 11.414l1.293 1.293a1 1 0 001.414-1.414L11.414 10l1.293-1.293a1 1 0 00-1.414-1.414L10 8.586 8.707 7.293z" clipRule="evenodd" />
                  </svg>
                </div>
                <div className="ml-3">
                  <p className="text-sm font-medium text-red-800">{error}</p>
                </div>
              </div>
            </div>
          )}

          {loading ? (
            <div className="flex justify-center items-center py-8">
              <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-blue-500"></div>
            </div>
          ) : (
            <Table
              headers={tableHeaders}
              data={colegios.map(c => ({
                id: c.id,
                nombre: c.nombre_colegio || '',
                codigo: c.codigo_colegio || '',
                municipio: getMunicipioNombre(c.id_municipio)
              }))}
              onEdit={handleEdit}
              onDelete={handleDelete}
            />
          )}
        </div>
      </div>
    </div>
  );
};

export default ColegiosPage;
