import React, { useState, useEffect } from 'react';
import Table from '../common/Table';
import Form from '../common/Form';

interface Departamento {
  id: number;
  nombre_departamento: string;
  codigo_departamento: string;
}

const DepartamentosPage: React.FC = () => {
  const [departamentos, setDepartamentos] = useState<Departamento[]>([]);
  const [editingDepartamento, setEditingDepartamento] = useState<Departamento | null>(null);
  const [formValues, setFormValues] = useState({
    nombre_departamento: '',
    codigo_departamento: ''
  });
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  const fetchDepartamentos = async () => {
    try {
      setLoading(true);
      setError(null);
      const response = await fetch('http://localhost:3001/api/departamentos');
      if (!response.ok) {
        throw new Error('Error al cargar los departamentos');
      }
      const data = await response.json();
      setDepartamentos(data);
    } catch (error) {
      console.error('Error al cargar departamentos:', error);
      setError('Error al cargar los departamentos. Por favor, intente nuevamente.');
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchDepartamentos();
  }, []);

  const handleFormChange = (name: string, value: string) => {
    setFormValues(prev => ({ ...prev, [name]: value }));
  };

  const handleSubmit = async () => {
    try {
      setError(null);
      const url = editingDepartamento
        ? `http://localhost:3001/api/departamentos/${editingDepartamento.id}`
        : 'http://localhost:3001/api/departamentos';
      
      const method = editingDepartamento ? 'PUT' : 'POST';
      
      const response = await fetch(url, {
        method,
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(formValues),
      });

      if (!response.ok) {
        throw new Error('Error en la operaci√≥n');
      }

      await fetchDepartamentos();
      setFormValues({ nombre_departamento: '', codigo_departamento: '' });
      setEditingDepartamento(null);
    } catch (error) {
      console.error('Error al guardar departamento:', error);
      setError('Error al guardar el departamento. Por favor, intente nuevamente.');
    }
  };

  const handleEdit = (departamento: Departamento) => {
    setEditingDepartamento(departamento);
    setFormValues({
      nombre_departamento: departamento.nombre_departamento,
      codigo_departamento: departamento.codigo_departamento,
    });
  };

  const handleDelete = async (departamento: Departamento) => {
    if (!window.confirm('¬øEst√° seguro de eliminar este departamento?')) {
      return;
    }

    try {
      setError(null);
      const response = await fetch(`http://localhost:3001/api/departamentos/${departamento.id}`, {
        method: 'DELETE',
      });

      if (!response.ok) {
        throw new Error('Error al eliminar');
      }

      await fetchDepartamentos();
    } catch (error) {
      console.error('Error al eliminar departamento:', error);
      setError('Error al eliminar el departamento. Por favor, intente nuevamente.');
    }
  };

  const formFields = [
    {
      name: 'nombre_departamento',
      label: 'Nombre del Departamento',
      type: 'text' as const,
    },
    {
      name: 'codigo_departamento',
      label: 'C√≥digo del Departamento',
      type: 'text' as const,
    },
  ];

  const tableHeaders = ['ID', 'Nombre', 'C√≥digo'];

  return (
    <div className="space-y-8">
      <div className="bg-white rounded-xl shadow-md overflow-hidden">
        <div className="p-6">
          <div className="flex items-center justify-between mb-6">
            <h2 className="text-xl font-semibold text-gray-900">
              {editingDepartamento ? 'Editar Departamento' : 'Nuevo Departamento'}
            </h2>
            {editingDepartamento && (
              <button
                onClick={() => {
                  setEditingDepartamento(null);
                  setFormValues({ nombre_departamento: '', codigo_departamento: '' });
                }}
                className="text-sm text-gray-600 hover:text-gray-900"
              >
                Cancelar edici√≥n
              </button>
            )}
          </div>
          <Form
            fields={formFields}
            values={formValues}
            onChange={handleFormChange}
            onSubmit={handleSubmit}
            submitLabel={editingDepartamento ? 'Actualizar' : 'Crear'}
          />
        </div>
      </div>

      <div className="bg-white rounded-xl shadow-md overflow-hidden">
        <div className="p-6">
          <div className="flex items-center justify-between mb-6">
            <h2 className="text-xl font-semibold text-gray-900">Lista de Departamentos</h2>
            <button
              onClick={fetchDepartamentos}
              className="inline-flex items-center rounded-md bg-gray-50 px-3 py-1 text-sm font-medium text-gray-700 hover:bg-gray-100 focus:outline-none focus:ring-2 focus:ring-gray-500 focus:ring-offset-2 transition-colors duration-200"
            >
              Actualizar lista
            </button>
          </div>
          
          {error && (
            <div className="mb-4 rounded-md bg-red-50 p-4">
              <div className="flex">
                <div className="flex-shrink-0">
                  <svg className="h-5 w-5 text-red-400" viewBox="0 0 20 20" fill="currentColor">
                    <path fillRule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zM8.707 7.293a1 1 0 00-1.414 1.414L8.586 10l-1.293 1.293a1 1 0 101.414 1.414L10 11.414l1.293 1.293a1 1 0 001.414-1.414L11.414 10l1.293-1.293a1 1 0 00-1.414-1.414L10 8.586 8.707 7.293z" clipRule="evenodd" />
                  </svg>
                </div>
                <div className="ml-3">
                  <p className="text-sm font-medium text-red-800">{error}</p>
                </div>
              </div>
            </div>
          )}

          {loading ? (
            <div className="flex justify-center items-center py-8">
              <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-blue-500"></div>
            </div>
          ) : (
            <Table
              headers={tableHeaders}
              data={departamentos.map(d => ({
                id: d.id,
                nombre: d.nombre_departamento,
                codigo: d.codigo_departamento,
              }))}
              onEdit={handleEdit}
              onDelete={handleDelete}
            />
          )}
        </div>
      </div>
    </div>
  );
};

export default DepartamentosPage;
